.cases {
    .text {
        padding-left: $size-20px;
    }

    &__blocks {
        margin-bottom: $size-70px;
    }
   
    &__big-cards {
        margin-bottom: $size-30px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        gap: $size-20px;
    }

    &__big-card-img {
        margin-bottom: 10px;
        height: clamp(100px, 27vw, 500px);
        background: $grey-border-color;
        border-radius: 30px;
        overflow: hidden;

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }

    }

    &__small-cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: auto;
        gap: $size-20px;


    }

    &__small-card-img {
        margin-bottom: 10px;
        height: clamp(100px, 22.2vw, 500px);
        background: $grey-border-color;
        border-radius: 30px;
        overflow: hidden;

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }

    }

    &__button-wrapper{
        display: flex;
        justify-content: center;
    }

    .button {
        text-transform: uppercase;
    }
}


