@import 'fonts';
@import 'variables';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    padding: 0;
    margin: 0;
    background: #DBE5E7;
}

p {
    margin-bottom: 0;
}

img {
    border: none;
}

header, main, footer {
    margin: auto;
    max-width: 1920px;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

.main-container {
    position: relative;
    background: $white-color;
    margin: auto;
    max-width: 1920px;
    overflow: hidden;
}

.container {
    width: 100%;
    margin: auto;
    padding: 0 $size-70px;
}

.button {
    padding: 0 14px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: clamp(18px, 2.34vw, 60px);
    font-family: "Golos Text", sans-serif;
    font-weight: 500;
    font-style: normal;
    border-radius: 17px;
    font-size: $size-14px;
    background: none;

    &_middle {
        height: clamp(30px, 4.16vw, 90px);
    }

    &_big {
        height: clamp(30px, 5.97vw, 114px);
        font-size: clamp(10px, 1.94vw, 40px);
        padding-left: clamp(10px, 4.72vw, 90px);
        padding-right: clamp(10px, 4.72vw, 90px);
        border-radius: clamp(20px, 3vw, 60px);
    }
}

.border-gray {
    border: 1px solid $grey-border-color;
    color: $black-color;
}

.dark-bg {
    background: $dark-background;
    color: $white-color;
}

.light-blue-bg {
    background: $light-blue-background;
    color: $white-color;
    text-transform: uppercase;
}



//whatsapp-link
.whatsapp {
    font-size: $size-15px;

    &::before {
        padding-right: 5px;
        color: #25D366;
    }
}

//main title block
.title-block {
    margin: auto;
    padding-top: 50px;
    padding-bottom: 20px;
    text-align: center;

    h1 {
        margin: auto;
        margin-bottom: 30px;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: clamp(20px, 3.47vw, 65px);
        line-height: 1.05;
    }
    
    p {
        margin: auto;
        margin-bottom: 30px;
        max-width: clamp(250px, 51.94vw, 1000px);
    }
}

.text {
    font-family: 'Golos Text', sans-serif;
    font-weight: 400;
    font-size: clamp(8px, 1.24vw, 23px);
    line-height: 1.3;
    color: $black-color;
}

//feedback block
.feedback-block {
    margin-bottom: $size-70px;
    text-align: center;

    &__link {
        margin-left: 20px;
        color: $black-color;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: $size-14px;
        line-height: 1.2;
        text-transform: uppercase;
    }
}

.banner {
    margin-bottom: 70px;
    width: 100%;

    &__img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

}

.key-word {
    display: inline-block;
    padding: 6px 14px;
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: $size-14px;
    line-height: 1.2;
    text-transform: uppercase;
    border-radius: 17px;
    border: 1px solid $grey-border-color;
}

.key-word-layout {
    margin-bottom: $size-30px;
}

.second-title {
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: clamp(16px, 2.22vw, 41px);
    line-height: 1.16;
    color: $black-color;
}

.third-title {
    margin-bottom: $size-20px;
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: $size-24px;
    line-height: 1.16;
    color: $black-color;
}

.key-words-title {
    margin-bottom: $size-20px;
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: $size-15px;
    line-height: 1.16;
    color: #98A8AE;;
}

.section {
    margin-bottom: $size-70px;
}


.feedback {
   &__wrapper {
    padding: clamp(20px, 3.46vw, 60px);
    display: flex;
    background: #EFF4F5;
    border-radius: 30px;
    justify-content: space-between;
    align-items: center;
    
    .second-title {
        width: clamp(200px, 34vw, 800px);

        &_string {
            display: block;
        }
    }
   }
}










.plan {
    .second-title {
        margin-bottom: $size-20px;
    }

}





