.our-business {
    text-align: center;  

    &__key-word {
        margin-bottom: 30px;
        color: $grey-key-word-color;
    }

    .second-title {
        max-width: clamp(320px, 64vw, 1186px);
        margin: auto;
        margin-bottom: $size-50px;
    }

    //big cards
    &__big-cards {
        margin-bottom: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }

    &__big-card {
        max-width: 848px;
        padding: $size-30px;
        flex-grow: 1;
        height: 100px;
        background: $dark-background;
        border-radius: 30px;
        height: clamp(100px, 20.8vw, 400px);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom;
        color: $light-gray-color;
        font-family: 'Golos Text', sans-serif;
        font-weight: 400;
        line-height: 1.1;
        text-align: left;
        cursor: pointer;
        transition: all .2s ease-in-out;

        &:first-child {
            background-image: url('../img/big-card-bg-image-1.svg');
        }
        &:last-child {
            background-image: url('../img/big-card-bg-image-2.svg');
        }

        .text {
            color: $white-color;
        }

        &:hover {
            background-color: #0b4655;
        }


    }

    &__small-cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: $size-20px;
        grid-auto-rows: clamp(100px, 14vw, 500px);
    }

    &__small-card {
        padding: $size-30px;
        border-radius: 30px;
        border: 1px solid $grey-border-color;
        text-align: left;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: all .2s ease-in-out;
        cursor: pointer;
        box-sizing: border-box;

        .text {
            line-height: 1.1;
        }

        i {
            font-size: $size-40px;
            line-height: 0.8;
            color: $dark-background;
        }

        &:nth-child(1) {
            .text {
                width: clamp(50px, 15.4vw, 290px);
            }
        }
        &:nth-child(2) {
            .text {
                width: clamp(50px, 15.4vw, 295px);
            }
        }
        &:nth-child(4) {
            .text {
                width: clamp(50px, 14.4vw, 220px);
            }
        }
        &:nth-child(5) {
            .text {
                width: clamp(50px, 15.4vw, 295px);
            }
        }

        &:hover {
            background: $dark-background;
            color: $white-color;
            
            .text {
                color: $white-color;
            }

            i {
                color: $white-color;
            }
        }
    }

    .text {
        margin-bottom: clamp(2px, 0.8vw, 20px);
    }

    &__small-text {
        font-size: clamp(8px, 0.9vw, 17px);
        line-height: 1.2;
    }
}
