@media (max-width: 768px) {
    .container {
        padding: 0 4vw;
    }

    .section {
        margin-bottom: $mb-sections-mobile;
    }

    .button {
        padding: 3.8vw;
        border-radius: 5vw;
        height: 8.6vw;
        font-size: 3.5vw;

        &_big {
            font-size: 5.6vw;
            padding: 8.6vw 12vw;
            border-radius: 44px;
        }
    }

    .whatsapp {
        height: 7.1vw;
        font-size: 3vw;
    }

    .title-block {
        padding-top: 10vw;

        h1 {
            margin-bottom: 4vw;
            font-size: 7.1vw;
            max-width: 100%;
            line-height: 1.16;
        }

        p {  
            margin-bottom: 1vw;
            max-width: 100%;
        }
    }

    .text {
        font-size: 4.1vw;
        line-height: 1.36;    
    }

    .feedback-block {
        margin-bottom: $mb-sections-mobile;
        &__link {
            margin-left: 3vw;
            font-size: 3.5vw;
        }
    }

    .banner {
        margin-bottom: $mb-sections-mobile;
    }

    .key-word {
        font-size: 3vw;
        padding: 1.8vw 2.6vw;
    }

  

    .feedback {
        &__wrapper {
            padding: 6vw;
            gap: 5vw;
            flex-direction: column;
        }

        .second-title {
            text-align: center;
        }

        .second-title, .form {
            width: 100%;
        }
    }

   

    .second-title {
        font-size: 5.4vw;
        line-height: 1.22;
    }

    .third-title {
        font-size: 5.38vw;
    }

  

   

    

 



    
    
}