@media (max-width: 768px) {
    .header {

        .menu-burger {
            display: inline-flex;
            flex-direction: column;
            width: min-content;
            width: 8vw;
            height: 8vw;

            img {
                object-fit: contain;
                height: 100%;
                width: 100%;
            }

            // &__line {
            //     display: block;
            //     // width: 24px;
            //     width: 6vw;
            //     height: 2px;
            //     height: 0.4vw;
            //     background: $black-color;

            //     &:not(:last-child) {
            //         margin-bottom: 1.2vw;
            //     }
            // }
        }

        &__feedback_desk {
            display: none;
        }

        &__feedback_mob {
            display: block;
        }

        &__box {
            display: grid;
            grid-template-columns: 1fr 1fr;
            align-items: center;
        }

        .logo {
            width: 34vw;
            height: 11vw;
        }

        &__menu {
            position: absolute;
            top: 100%;
        }

        &__contact {
            display: flex;
            align-items: center;
        }

        &__whatsapp {
            margin-right: 3vw;
        }
    }
}