@media (max-width: 768px) {
    .layout {
        flex-direction: column;

        &__right-side, &__left-side {
            width: 100%;
        }

        .second-title {
            margin-bottom: 4.1vw;
        }
        
        &__left-side {
            .key-word {
                margin-bottom: 5vw;
            }

            .third-title {
                margin-bottom: 4.1vw;
            }
        }

        .key-words-title {
            margin-bottom: 4vw;
            font-size: 3.9vw;
        }
    }
}